<template>
  <el-container class="wrap-base">
    <el-header class="align-center">{{ pageTitle }}</el-header>
    <el-container class="wrap-content flex">
      <transition name="slide">
        <visual-left-aside style="z-index: 999" v-show="fold" :fold="fold">
          <!-- <i class="el-icon-s-fold color-blue ft-20" @click="foldHandle"></i> -->
        </visual-left-aside>
      </transition>
      <el-container class="map-content flex-1">
        <!-- <visual-map></visual-map> -->
        <!-- <i class="el-icon-s-unfold color-blue ft-20" @click="unFoldHandle"></i> -->
        <div id="mapDiv" :key="timer"></div>
        <el-main>
          <div class="grid-show" v-show="show2" @click="handleClick">
            <!-- <div class="grid-title">{{ gridData.name }}</div> -->
            <div class="grid-title">问题信息</div>
            <div class="grid-detail flex">
              <figure>
                <figcaption>检查部位</figcaption>
                <!-- <h2 class="big-des">{{ gridData.gridName }}</h2> -->
                <h2 class="big-des">本部</h2>
              </figure>
              <figure>
                <figcaption>问题类别</figcaption>
                <!-- <h2 class="big-des">{{ gridData.factory }}</h2> -->
                <h2 class="big-des">防控</h2>
              </figure>
              <figure>
                <figcaption>反馈期限</figcaption>
                <!-- <h2 class="big-des">{{ gridData.principal }}</h2> -->
                <h2 class="big-des">2021.06.28</h2>
              </figure>
              <figure>
                <figcaption>问题描述</figcaption>
                <!-- <h2 class="big-des">{{ gridData.tel }}</h2> -->
                <h2 class="big-des">{{ nowData.name }}</h2>
              </figure>
            </div>
            <div class="time-line">
              <div class="time-line-one">
                <h3>问题录入</h3>
                <p>由于长时间运行，导致设备故障</p>
                <img src="../assets/images/time1.png" alt="" />
              </div>
              <div class="time-line-two">
                <h3>问题整改</h3>
                <div v-if="nowData.name == '墙体断裂'">
                  <p>整改人：李松明</p>
                  <p>投入金额：500000元</p>
                  <img src="../assets/images/time1.png" alt="" />
                </div>
              </div>
              <div class="time-line-three">
                <h3>问题复查</h3>
              </div>
            </div>
          </div>
          <visual-map-storage
            v-if="show"
            @showgridhandle="showGrid"
            @closehandle="show = false"
          ></visual-map-storage>
        </el-main>
      </el-container>
      <transition name="slide">
        <visual-right-aside v-show="fold" :fold="fold"></visual-right-aside>
      </transition>
    </el-container>
    <!-- 底部导航 -->
    <el-footer>
      <ul class="map-nav flex">
        <li
          v-for="(item, index) in mapNavArr"
          :key="index"
          @click="showModel(item)"
        >
          <img :src="item.imgUrl" alt="" />
          <div>{{ item.content }}</div>
        </li>
      </ul>
    </el-footer>
    <!-- 左右下背景 -->
    <div class="left" @click="foldHandle"></div>
    <div class="right" @click="foldHandle"></div>
    <div class="bottom"></div>
  </el-container>
</template>
<script>
import VisualLeftAside from '@/components/visual/VisualLeftAside.vue'
import VisualRightAside from '@/components/visual/VisualRightAside.vue'
import VisualMap from '@/components/visual/VisualMap.vue'
import VisualMapStorage from '@/components/visual/VisualMapStorage.vue'
import { mapdata, mapEntName } from '../utils/data'
export default {
  name: 'DataCenter',
  components: {
    VisualLeftAside,
    VisualRightAside,
    VisualMapStorage,
    VisualMap,
  },
  data() {
    return {
      nowData: '', //当前点击标注数据
      MarkerArray: [], //标注数据
      entNameShow: false,
      MarkerZDArray: [], //重大风险源标注数据
      ZDShow: false,
      markers:[],//所有覆盖点物
      pageTitle: '天津港本质安全综合管控平台',
      mapNavArr: [
        {
          id: 1,
          imgUrl: require('@/assets/images/点击前/企业.png'),
          activeImgUrl: require('@/assets/images/点击后/企业.png'),
          content: '企业',
        },
        {
          id: 2,
          imgUrl: require('@/assets/images/点击前/重大危险源.png'),
          activeImgUrl: require('@/assets/images/点击后/重大危险源.png'),
          content: '重大危险源',
        },
        {
          id: 3,
          imgUrl: require('@/assets/images/点击前/危险作业.png'),
          activeImgUrl: require('@/assets/images/点击后/危险作业.png'),
          content: '危险作业',
        },
        {
          id: 4,
          imgUrl: require('@/assets/images/点击前/摄像头.png'),
          activeImgUrl: require('@/assets/images/点击后/摄像头.png'),
          content: '摄像头',
        },
        {
          id: 5,
          imgUrl: require('@/assets/images/点击前/消防站.png'),
          activeImgUrl: require('@/assets/images/点击后/消防站.png'),
          content: '消防站',
        },
        {
          id: 6,
          imgUrl: require('@/assets/images/点击前/消防栓.png'),
          activeImgUrl: require('@/assets/images/点击后/消防栓.png'),
          content: '消防栓',
        },
        {
          id: 7,
          imgUrl: require('@/assets/images/点击前/应急物资.png'),
          activeImgUrl: require('@/assets/images/点击后/应急物资.png'),
          content: '应急物资',
        },
        {
          id: 8,
          imgUrl: require('@/assets/images/点击前/特种设备.png'),
          activeImgUrl: require('@/assets/images/点击后/特种设备.png'),
          content: '特种设备',
        },
      ],
      marker: null,
      map: null,
      zoom: 12,
      show: false,
      show2: false,
      fold: true,
      timer: '',
      gridData: {
        name: '预警数量',
        gridName: '网格一',
        factory: '东疆港',
        principal: '张无忌',
        tel: 13625665589,
      },
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    handleClick() {
      window.open('http://59.110.236.3:8123/#/ProblemParameter')
    },

    // 初始化
    init() {
      this.$nextTick(function () {
        this.renderMap()
      })
    },

    // 点击左右折叠效果
    foldHandle() {
      this.fold = !this.fold
      //  console.log(this.fold,"......")
    },

    // 子组件南丁格尔点击事件传值
    showGrid(data) {
      // console.log('data:', data)
      this.gridData.name = data.name

      // 创建点的标记
      this.createMarkers()
      // 原覆盖物
      this.drawOverlay()
      // 地图级别
      if(this.zoom != 14){
        this.zoom = 14
      }
      this.map.centerAndZoom(new T.LngLat(117.77, 39), this.zoom)
    },

    // 天地图部分
    renderMap() {
      // 设置地图级别
      // this.zoom = 12
      //初始化地图对象
      this.map = new T.Map('mapDiv')
      //设置显示地图的中心点和级别
      this.map.centerAndZoom(new T.LngLat(117.77, 39), this.zoom)
      //创建缩放平移控件对象
      var control = new T.Control.Zoom()
      //添加缩放平移控件
      this.map.addControl(control)
      // 设置地图显示范围
      // this.map.setMaxBounds(
      //   new T.LngLatBounds(
      //     new T.LngLat(117.65, 38.92),
      //     new T.LngLat(117.89, 39.09)
      //   )
      // )
      //允许鼠标滚轮缩放地图
      this.map.enableScrollWheelZoom()
      // 允许双击放大地图
      // this.map.enableDoubleClickZoom()
      //禁用双击放大地图
      this.map.disableDoubleClickZoom()
      // 允许通过小键盘 + - 按钮缩放地图
      this.map.enableKeyboard()

      var imageURL =
        'http://t0.tianditu.gov.cn/img_w/wmts?' +
        'SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=img&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles' +
        '&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&tk=5b02fc2503dcd0c9d3abf727cd984fd2'
      //创建自定义图层对象
      var lay = new T.TileLayer(imageURL, {
        minZoom: 1,
        maxZoom: 18,
        opacity: 0.8,
      })

      //绘制矩形网格
      this.drawOverlay()

      //将图层增加到地图上
      this.map.addLayer(lay)

      // 创建企业名称标记
      for (let i of mapEntName) {
        this.addMarker(i)
      }

      //创建重大标注
      this.addMarkerZD()
    },

    //绘制矩形网格
    drawOverlay() {
      for (let i of mapdata) {
        // console.log(i)
        switch (i.type) {
          case 5:
            //多边形
            //创建面对象
            let polygon = new T.Polygon(i.latlng, i.options)
            polygon.addEventListener('click', this.clickEvent)
            //向地图上添加面
            this.map.addOverLay(polygon)
            break
          case 4:
            //线
            //创建线对象
            let line = new T.Polyline(i.latlng, i.options)
            line.addEventListener('click', this.clickEvent)
            //向地图上添加线
            this.map.addOverLay(line)
            break
          case 6:
            //矩形
            let bounds = new T.LngLatBounds(i.latlng[0], i.latlng[2])
            let rect = new T.Rectangle(bounds)
            rect.addEventListener('click', this.clickEvent)
            this.map.addOverLay(rect)
            break
          case 8:
            //圆
            let circle = new T.Circle(i.latlng, i.rY / 2, i.options)
            circle.addEventListener('click', this.clickEvent)
            //向地图上添加圆
            this.map.addOverLay(circle)
            break
        }
      }
    },

    clickEvent() {
      this.show = true
    },

    createMarkers() {
      var markers = [
        // { lng: 117.703925, lat: 39.049074 },
        // { lng: 117.750925, lat: 39.032074 },
        { name: '墙体断裂', lng: 117.765925, lat: 39.001074 },
        { name: '墙体倾斜', lng: 117.772825, lat: 39.005174 },
      ]

      markers.forEach((v, i) => {
        //创建图片对象
        let icon = new T.Icon({
          iconUrl:
            i % 2 == 0
              ? require('../assets/images/pl.png')
              : require('../assets/images/pl2.png'),
          iconSize: new T.Point(30, 26),
          iconAnchor: new T.Point(10, 20),
        })
        //向地图上添加自定义标注
        let marker = new T.Marker(new T.LngLat(v.lng, v.lat), {
          icon: icon,
        })

        // var infoWin1 = new T.InfoWindow()
        // var sContent = `<div style="width:80px;height:49px;margin-bottom:50px">
        //   <p style="color:white">${i % 2 == 0 ? '预警' : '事件'}</p>
        //   <p style="font-weight:bold;">1280</p>
        // </div>`
        // let name = this.gridData.name
        let sContent = `<div style="width:80px;height:49px;margin-bottom:50px">
          <p style="color:white">${v.name}</p>
          <p style="font-weight:bold;">2021/06/30</p>
        </div>`

        // 先清除原来的覆盖物点
        // this.map.clearOverLays()
        // 再添加新的覆盖物标记点
        this.createInfo(v.lng, v.lat, sContent)
        this.map.addOverLay(marker)
        marker.addEventListener('click', () => {
          this.show2 = true
          this.nowData = v
        })
      })
    },

    //创建信息窗口
    createInfo(lng, lat, content) {
      // 通过经纬度创建位置对象
      let lnglat = new T.LngLat(lng, lat)
      //创建信息窗口对象
      let infoWin = new T.InfoWindow()
      // 窗口对象中设置经纬度信息
      infoWin.setLngLat(lnglat)
      //设置信息窗口要显示的内容
      infoWin.setContent(content)
      //向地图上添加信息窗口
      this.map.addOverLay(infoWin)
    },
    // 底部导航点击事件函数
    showModel(item) {
      switch (item.id) {
        case 1:
          //企业
          this.entNameShow = !this.entNameShow
          for (let i of this.MarkerArray) {
            if (this.entNameShow) {
              i.show()
            } else {
              i.hide()
            }
          }
          break

        case 2:
          //企业
          this.ZDShow = !this.ZDShow
          for (let i of this.MarkerZDArray) {
            if (this.ZDShow) {
              i.show()
            } else {
              i.hide()
            }
          }
          break
      }
    },
    // 创建企业名称标记
    addMarker(item) {
      //创建图片对象
      let icon = new T.Icon({
        iconUrl: require('../assets/images/标记点/企业.png'),
        iconSize: new T.Point(19, 27),
        iconAnchor: new T.Point(10, 25),
      })
      //向地图上添加自定义标注
      let marker = new T.Marker(new T.LngLat(item.lng, item.lat), {
        icon: icon,
      })
      marker.addEventListener('click', this.clickEvent)
      this.MarkerArray.push(marker)
      this.map.addOverLay(marker) // 将标注添加到地图中
      let label = new T.Label({
        text: `<b>${item.name}<b>`,
        position: marker.getLngLat(),
        offset: new T.Point(3, -12),
      })
      label.setBackgroundColor('#ffffff7a')
      this.MarkerArray.push(label)
      this.map.addOverLay(label)
      marker.hide()
      label.hide()
    },
    // 添加重大标注
    addMarkerZD() {
      let temp = [
        { lng: 117.78339, lat: 39.03732 },
        { lng: 117.75524, lat: 39.06638 },
        { lng: 117.75146, lat: 39.00505 },
        { lng: 117.80468, lat: 39.00718 },
        { lng: 117.86442, lat: 38.93885 },
        { lng: 117.73945, lat: 38.97169 },
        { lng: 117.69241, lat: 38.95407 },
      ]
      for (let item of temp) {
        //创建图片对象
        let icon = new T.Icon({
          iconUrl: require('../assets/images/标记点/重大危险源.png'),
          iconSize: new T.Point(19, 27),
          iconAnchor: new T.Point(10, 25),
        })
        //向地图上添加自定义标注
        let marker = new T.Marker(new T.LngLat(item.lng, item.lat), {
          icon: icon,
        })
        this.map.addOverLay(marker) // 将标注添加到地图中
        this.MarkerZDArray.push(marker)
        marker.hide()
      }
    },
  },
  mounted(){
    
  }
}
</script>
<style lang="less" scoped>
// 整体框架
.wrap-base {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 4%;
  // background: url(../assets/images/base_wrap_bg.jpg);
  background: url(../assets/images/bg-all.png);
}

// 中间容器
.wrap-content {
  box-sizing: border-box;
  position: relative;
  margin: 10px 0 1%;
  z-index: 1;
}

.time-line {
  height: 370px;
  width: 260px;
  margin-top: 20px;
  background: rgba(3, 45, 87, 0.8) url(../assets/images/time.png) 30px 20px
    no-repeat;
  background-size: auto 90%;
  position: relative;

  & > div {
    position: absolute;
    left: 80px;
    line-height: 2;
  }

  &-one {
    top: 26px;
    color: #0fcbbc;
  }

  &-two {
    top: 146px;
    h3 {
      color: #0fa7e8;
    }
  }

  &-three {
    top: 310px;
    color: gray;
  }
}

// 页面头部
.el-header {
  height: 108px !important;
  font-size: 30px;
  font-weight: bold;
  color: #14e6f7;
  letter-spacing: 8px;
  line-height: 108px;
  background: url(../assets/images/big-top.png) top center no-repeat;
}

// 左右边栏
.el-aside {
  height: calc(100% + 30px);
  position: absolute;
  right: -40px;
  top: -30px;
  z-index: 1111;
  background: url(../assets/images/big-right.png) top center no-repeat,
    url(../assets/images/bg-all.png);
  background-size: 100% 100%;
  figure {
    margin: 4px 18px 0 14px;
    overflow: hidden;
  }
  overflow: hidden;
}

.el-aside.aside-left {
  right: unset;
  left: -40px;
  background-image: url(../assets/images/big-left.png),
    url(../assets/images/bg-all.png);
  figure {
    margin-left: 30px;
  }
}

.map-content {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  position: relative;
}
::v-deep .el-main {
  height: 100%;
  padding: 0;
  position: relative;
}

// 底部导航

.el-footer {
  width: 60%;
  height: 100px !important;
  left: 50%;
  bottom: 50px;
  z-index: 1000;
  transform: translateX(-50%);
  line-height: 24px;
  text-align: center;
  position: fixed;
  font-size: 12px;
  background: transparent !important;

  img {
    width: 70px;
  }
}
.map-nav {
  justify-content: space-around;
  li {
    color: #14e6f7;
    font-weight: bold;
    cursor: pointer;
  }
}

// 地图样式
#mapDiv {
  width: 100%;
  height: calc(100% - 20px);
  position: absolute;
  z-index: 100;
}

// 地图左上角默认放大缩小按钮
::v-deep .tdt-left {
  display: none;
}

// 地图 marker 标记点
::v-deep .tdt-label {
  // background-color: rgba(255,255,255,.82)!important;
  background-color: rgba(0, 0, 0, 0.52) !important;
  color: white !important;
  border: none !important;
}
::v-deep .tdt-fade-anim .tdt-map-pane .tdt-infowindow {
  bottom: 10px !important;
}
::v-deep .tdt-container a.tdt-infowindow-close-button,
::v-deep .tdt-infowindow-tip-container {
  display: none;
}
::v-deep .tdt-infowindow:nth-of-type(odd) .tdt-infowindow-content-wrapper {
  background: transparent;
  box-shadow: unset;
  color: #f0f11b;
  background: url(../assets/images/yj.png) no-repeat 24px 30px;
}
::v-deep .tdt-infowindow:nth-of-type(even) .tdt-infowindow-content-wrapper {
  background: transparent;
  box-shadow: unset;
  color: #01e5fe;
  background: url(../assets/images/yj2.png) no-repeat 24px 30px;
}
::v-deep .tdt-infowindow-content {
  position: relative;
  top: 20px;
  p {
    margin: unset;
    padding: 0 10px;
    line-height: 1.6;
  }
}

// 折叠按钮
// .el-icon-s-fold {
//   position: absolute;
//   right: 14px;
//   top: 30px;
//   cursor: pointer;
// }

.slide-enter-active {
  animation: float-in 1s;
}
.slide-leave-active {
  animation: float-out 1s;
}

@keyframes float-in {
  from {
    width: 0;
    height: calc(100% + 30px);
    opacity: 0;
  }
  to {
    width: 300px;
    height: calc(100% + 30px);
    opacity: 1;
  }
}
@keyframes float-out {
  from {
    width: 300px;
    height: calc(100% + 30px);
    opacity: 1;
  }
  to {
    width: 0;
    height: calc(100% + 30px);
    opacity: 1;
  }
}

// 展开按钮
// .el-icon-s-unfold {
//   position: absolute;
//   left: -24px;
//   cursor: pointer;
// }

.mt-20 {
  margin-top: 20px !important;
}

// 左右下背景
.left {
  position: fixed;
  left: 0;
  top: 18%;
  z-index: 10;
  width: 28px;
  height: 100%;
  cursor: pointer;
  background: url(../assets/images/left.png) left no-repeat;
}
.right {
  position: fixed;
  right: 0;
  top: 18%;
  z-index: 10;
  width: 28px;
  height: 100%;
  cursor: pointer;
  background: url(../assets/images/right.png) right no-repeat;
}
.bottom {
  position: fixed;
  bottom: -20px;
  left: 0;
  height: 315px;
  width: 100%;
  background: url(../assets/images/bottom.png) bottom no-repeat;
  background-size: 100% 100%;
  // z-index: 100;
  opacity: 0.5;
}
</style>