<template>
  <div id="mapDiv"></div>
</template>
<script>
export default {
  name: 'VisualMap',
  data() {
    return {
      marker: null,
      map: null,
    }
  },
  methods: {
    // 天地图部分
    renderMap() {
      // 设置地图级别
      var zoom = 12
      //初始化地图对象
      this.map = new T.Map('mapDiv')
      //设置显示地图的中心点和级别
      this.map.centerAndZoom(new T.LngLat(117.77, 39.05), zoom)
      //创建缩放平移控件对象
      var control = new T.Control.Zoom()
      //添加缩放平移控件
      this.map.addControl(control)
      // 设置地图显示范围
      // this.map.setMaxBounds(
      //   new T.LngLatBounds(
      //     new T.LngLat(117.65, 38.92),
      //     new T.LngLat(117.89, 39.09)
      //   )
      // )
      //允许鼠标滚轮缩放地图
      this.map.enableScrollWheelZoom()
      // 允许双击放大地图
      this.map.enableDoubleClickZoom()
      // 允许通过小键盘 + - 按钮缩放地图
      this.map.enableKeyboard()

      var imageURL =
        'http://t0.tianditu.gov.cn/img_w/wmts?' +
        'SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=img&STYLE=default&TILEMATRIXSET=w&FORMAT=tiles' +
        '&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&tk=5b02fc2503dcd0c9d3abf727cd984fd2'
      //创建自定义图层对象
      var lay = new T.TileLayer(imageURL, {
        minZoom: 1,
        maxZoom: 18,
        opacity: 0.8,
      })
      //将图层增加到地图上
      this.map.addLayer(lay)

      this.createMarkers()
    },

    createMarkers() {
      var markers = [
        { lng: 117.703925, lat: 39.049074 },
        { lng: 117.750925, lat: 39.032074 },
        { lng: 117.763925, lat: 38.996074 },
        { lng: 117.693925, lat: 39.009074 },
        { lng: 117.783925, lat: 39.039074 },
      ]

      markers.forEach((v, i) => {
        //创建图片对象
        var icon = new T.Icon({
          iconUrl:
            i % 2 == 0
              ? require('../../assets/images/pl.png')
              : require('../../assets/images/pl2.png'),
          iconSize: new T.Point(30, 26),
          iconAnchor: new T.Point(10, 20),
        })
        //向地图上添加自定义标注
        var marker = new T.Marker(new T.LngLat(v.lng, v.lat), {
          icon: icon,
        })

        // var infoWin1 = new T.InfoWindow()
        var sContent = `<div style="width:80px;height:49px;margin-bottom:50px">
          <p style="color:white">${i % 2 == 0 ? '预警' : '事件'}</p>
          <p style="font-weight:bold;">1280</p>
        </div>`
        this.createInfo(v.lng, v.lat, sContent)

        this.map.addOverLay(marker)
        marker.addEventListener('click', this.MarkerClick)
      })
    },

    MarkerClick(e) {
      // alert(e.lnglat.getLng() + ',' + e.lnglat.getLat())
      this.gridTitle = ['事件', '预警'][parseInt(Math.random() * 2)]
    },

    //创建信息窗口
    createInfo(lng, lat, content) {
      // 通过经纬度创建位置对象
      let lnglat = new T.LngLat(lng, lat)
      //创建信息窗口对象
      let infoWin = new T.InfoWindow()
      // 窗口对象中设置经纬度信息
      infoWin.setLngLat(lnglat)
      //设置信息窗口要显示的内容
      infoWin.setContent(content)
      //向地图上添加信息窗口
      this.map.addOverLay(infoWin)
    },
  },
  mounted() {
    this.renderMap()
  },
}
</script>
<style lang="less" scoped>
#mapDiv {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 100;
}

// 地图左上角默认放大缩小按钮
::v-deep .tdt-left {
  display: none;
}

// 地图 marker 标记点
::v-deep .tdt-fade-anim .tdt-map-pane .tdt-infowindow {
  bottom: 10px !important;
}

::v-deep .tdt-container a.tdt-infowindow-close-button,
::v-deep .tdt-infowindow-tip-container {
  display: none;
}
::v-deep .tdt-infowindow:nth-of-type(odd) .tdt-infowindow-content-wrapper {
  background: transparent;
  box-shadow: unset;
  color: #f0f11b;
  background: url(../../assets/images/yj.png) no-repeat 24px 30px;
}
::v-deep .tdt-infowindow:nth-of-type(even) .tdt-infowindow-content-wrapper {
  background: transparent;
  box-shadow: unset;
  color: #01e5fe;
  background: url(../../assets/images/yj2.png) no-repeat 24px 30px;
}

::v-deep .tdt-infowindow-content {
  position: relative;
  top: 20px;
  p {
    margin: unset;
    padding: 0 10px;
    line-height: 1.6;
  }
}
</style>