<template>
  <el-aside class="aside-left">
    <slot></slot>
    <figure>
      <header class="risk-sts risk-left" style="margin-top: 20px">
        企业安全风险总览
      </header>
      <div id="company-sts-wrap" @click="handleClick(1)">
        <div id="company-sts"></div>
      </div>
    </figure>
    <figure>
      <header class="risk-sts risk-left risk-left2">隐患统计</header>
      <ul class="risk-count flex">
        <li>1</li>
        <li>1</li>
        <li>6</li>
        <li>0</li>
        <li>0</li>
        <li>0</li>
        <li class="other">项</li>
      </ul>
      <div id="hazard-sts" ref="hazard-sts" @click="handleClick(2)"></div>
      <div id="pie-chart" ref="pie-chart"></div>
      <div id="line-chart" ref="line-chart" @click="handleClick(4)"></div>
    </figure>
  </el-aside>
</template>
<script>
import * as echarts from 'echarts'
export default {
  name: 'VisualLeftAside',
  props: ['fold'],
  data() {
    return {
      timer: null,
      tag: true,
    }
  },
  watch: {
    fold(v) {
      console.log('v:', v)
      if (v) {
        this.init()
      }
    },
  },
  methods: {
    handleClick(type) {
      console.log(type)
      switch (type) {
        case 1:
          window.open('http://59.110.236.3:8123/#/SecurityFile')
          break
        case 2:
          window.open('http://59.110.236.3:8123/#/HiddenCheck')
          break
        case 4:
          // window.open('http://39.97.191.140:8087/#/map')
          window.open(
            'http://10.128.141.218:8080/safetyGroup/hiddenDanger/index/3'
          )
          break
      }
    },
    // 初始化
    init() {
      let arr = [
        this.showCompanyInit,
        this.showHazardInit,
        this.showLineInit,
        this.showPieInit,
      ]
      if (this.tag) {
        arr.forEach((v) => {
          return v()
        })
        this.tag = false
      } else {
        arr[parseInt(Math.random() * arr.length)]()
      }

      // this.$nextTick(function () {
      //   this.showCompanyInit()
      //   this.showHazardInit()
      //   this.showPieInit()
      //   this.showLineInit()
      // })
    },

    // 企业安全风险总览
    showCompanyInit() {
      // let chart = echarts.init(document.getElementById('company-sts'))
      // 解决echarts控制台警告 echarts.js?1be7:2168 There is a chart instance already initialized on the dom
      //有的话就获取已有echarts实例的DOM节点。
      let chart = echarts.getInstanceByDom(
        document.getElementById('company-sts')
      )
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('company-sts'))
      }

      var option = {
        radar: [
          {
            center: ['50%', '50%'], // 位置 百分比  或 某一数值  [300, 400]   [x, y]
            radius: 45, // 缩放比例   单一参数指定外半径   [number|string,number|string]    数组的第一项是内半径，第二项是外半径
            startAngle: 0, // 第一项为 0 度 指向正南方  显示器的右手边

            splitNumber: 6, //  雷达图分为几层
            shape: 'polygon', // 雷达图绘制类型  默认 polygon  多边形根据 indicator.length  可选  circle 圆形
            // 雷达图的指示器，用来指定雷达图中的多个变量（维度）
            indicator: [
              // 直接在这里修改  item 文字 也是可以的  优先级大于  name.textStyle.color
              // 注意文档没有提到  name.textStyle.color
              { text: '隐患排查', max: 150 },
              { text: '应急人员', max: 150 },
              { text: '管理机构和人员', max: 150 },
              { text: '安全责任体系', max: 120 },
              { text: '安全投入', max: 108 },
              { text: '生产工艺与\n设备设施', max: 120 },
              { text: '风险排查', max: 108 },
              { text: '作业管理', max: 80 },
              // { text: '风险排查', max: 108, color: 'white' },
            ],
            // 坐标轴在 grid 区域中的分隔区域，默认不显示
            splitArea: {
              show: false,
            },
            // 坐标轴在 grid 区域中的分隔线。
            splitLine: {
              lineStyle: {
                // 雷达图每层线圈的样式
                type: 'soild',
                color: '#055B9B',
              },
            },
          },
        ],
        series: [
          {
            name: '',
            type: 'radar',
            radarIndex: 0,
            data: [
              {
                value: [123, 143, 112, 97, 89, 68, 62, 61],
                name: '雷达图',
                lineStyle: {
                  // 雷达图边线
                  color: '#32A8CE',
                },
                symbol: 'none',
                areaStyle: {
                  // 渐变色
                  color: new echarts.graphic.RadialGradient(0.1, 1, 1, [
                    {
                      color: 'rgba(42,149,186,.5)',
                      offset: 0,
                    },
                    {
                      color: 'rgba(69,208,228,.5)',
                      offset: 1,
                    },
                  ]),
                },
              },
              {
                value: [67, 43, 72, 67, 45, 45, 70, 56],
                name: '雷达图',
                lineStyle: {
                  // 雷达图边线
                  color: '#32A8CE',
                },
                symbol: 'none',
                itemStyle: {
                  color: '#ff0000',
                },
                areaStyle: {
                  opacity: 1,
                  // 渐变色
                  color: new echarts.graphic.RadialGradient(0.1, 0.5, 1, [
                    {
                      color: 'rgba(8,89,183,.5)',
                      offset: 0,
                    },
                    {
                      color: 'rgba(209,210,43,.5)',
                      offset: 1,
                    },
                  ]),
                },
              },
            ],
          },
        ],
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 左侧隐患整改
    showHazardInit() {
      // 解决echarts控制台警告 echarts.js?1be7:2168 There is a chart instance already initialized on the dom
      //有的话就获取已有echarts实例的DOM节点。
      let chart = echarts.getInstanceByDom(this.$refs['hazard-sts'])
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(this.$refs['hazard-sts'])
      }

      var data = [
        {
          value: 100,
          name: '超期未整改数',
          unit: '项',
          itemStyle: {
            color: '#FEAE5C',
          },
        },
        {
          value: 700,
          name: '今日隐患数',
          unit: '项',
          itemStyle: {
            color: '#36E5FE',
          },
        },
        {
          value: 1152,
          name: '已整改隐患数',
          unit: '项',
          itemStyle: {
            color: 'greenyellow',
          },
        },
      ]
      var count = 0;
      var labelData = {
        tit:"整改率",
        per:87.5
      }
      var option = {
        tooltip: {
          trigger: 'item',
          show: false,
        },
        legend: {
          top: '10%',
          right: '6%',
          itemWidth: 8,
          itemHeight: 8,
          itemGap: 10,
          orient: 'vertical',
          icon: 'circle',
          formatter: (params) => {
            // console.log(params)
            // console.log(data[count])
            var result = `${params}\n${data[count]['value']}\t${data[count]['unit']}`;
            count++
            return result
          },
          textStyle: {
            color: '#fff',
            lineHeight: 16,
          },
        },
        series: [
          {
            name: '隐患统计',
            type: 'pie',
            radius: ['70%', '80%'],
            center: ['30%', '50%'],
            avoidLabelOverlap: false,
            startAngle: 150,
            label: {
              show: true,
              position: 'center',
              fontSize: 14,
              formatter: `{d|${labelData.per}}{p|%}\n{b|${labelData.tit}}`,
              
              // formatter:params => {
              //     return params.name + "\n" + params.percent + "%"
              // },
              // formatter: `{d|{d}}{p|%}\n{b|{b}}`,
              rich: {
                b: {
                  fontSize: 12,
                  color: '#B7BCC7',
                  padding: [10, 0, 0, 0],
                },
                d: {
                  color: '#34E4FE',
                  fontSize: 16,
                },
                p: {
                  color: '#fff',
                  fontSize: 16,
                },
              },
            },
            emphasis: {
              scale: false,
            },
            labelLine: {
              show: true,
            },
            data: data,
          },
          {
            type: 'pie',
            radius: ['94%', '98%'],
            center: ['30%', '50%'],
            startAngle: 150,
            emphasis: {
              //鼠标移入变大
              scale: false,
            },
            data: [
              {
                value: 100,
                itemStyle: {
                  color: '#0F3A61',
                },
              },
            ],
          },
        ],
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 左侧柱形图
    showPieInit() {
      let chart = echarts.getInstanceByDom(document.getElementById('pie-chart'))
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('pie-chart'))
      }
      let option = {
        grid: {
          top: '10%',
          left: '15%',
          right: '10%',
          bottom: '15%',
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow', // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        xAxis: {
          type: 'category',
          data: ['现场', '基础', '作业', '设备', '其他'],
          axisLabel: {
            color: '#fff',
          },
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            color: '#fff',
          },
        },
        series: [
          {
            data: [
              {
                value: 200,
                itemStyle: {
                  color: '#4269E3',
                },
              },
              {
                value: 200,
                itemStyle: {
                  color: '#44A5E2',
                },
              },
              {
                value: 150,
                itemStyle: {
                  color: '#E18E44',
                },
              },
              {
                value: 80,
                itemStyle: {
                  color: '#E34265',
                },
              },
              {
                value: 70,
                itemStyle: {
                  color: '#CD5BE9',
                },
              },
            ],
            type: 'bar',
          },
        ],
      }
      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 左侧底部折线图
    showLineInit() {
      let chart = echarts.getInstanceByDom(this.$refs['line-chart'])
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(this.$refs['line-chart'])
      }

      var option = {
        title: {
          text: '隐患统计',
          show: false,
        },
        tooltip: {
          trigger: 'axis',
        },
        legend: {
          // itemWidth: 14,
          itemHeight: 10,
          textStyle: {
            color: '#fff',
            fontSize: 12,
          },
        },
        grid: {
          left: '3%',
          right: '6%',
          bottom: '3%',
          containLabel: true,
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLabel: {
            color: '#fff',
          },
          data: [
            '1月',
            '2月',
            '3月',
            '4月',
            '5月',
            '6月',
            '7月',
            '8月',
            '9月',
            '10月',
            '11月',
            '12月',
          ],
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            color: '#fff',
          },
        },
        series: [
          {
            name: '现场',
            type: 'line',
            stack: '总量',
            data: [20, 12, 11, 34, 19, 23, 21, 18, 34, 12, 11, 20],
          },
          {
            name: '基础',
            type: 'line',
            stack: '总量',
            data: [22, 12, 19, 34, 20, 30, 31, 12, 11, 34, 19, 23],
          },
          {
            name: '作业',
            type: 'line',
            stack: '总量',
            data: [15, 32, 20, 15, 19, 33, 41, 34, 20, 30, 31, 12],
          },
          {
            name: '设备',
            type: 'line',
            stack: '总量',
            data: [32, 33, 30, 34, 39, 33, 32, 30, 34, 39, 33, 32],
          },
          {
            name: '其他',
            type: 'line',
            stack: '总量',
            data: [82, 32, 90, 34, 12, 100, 80, 18, 110, 56, 34, 90],
          },
        ],
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },
  },
  mounted() {
    this.init()
    clearInterval(this.timer)
    this.timer = setInterval(() => {
      this.init()
    }, 15000)
  },
}
</script>
<style lang="less" scoped>
// 左侧企业安全风险总览
#company-sts {
  height: 180px;
  width: 100%;
}

// 左侧隐患统计
ul.risk-count {
  width: 70%;
  margin: 6px;
  li {
    padding: 2px 4px;
    color: white;
    font-size: 16px;
    border: 1px solid #5decf1;

    &.other {
      align-self: flex-end;
      padding: 0;
      border: none;
      font-size: 16px;
    }
  }
}

// 左侧隐患分类统计
#hazard-sts {
  height: 130px;
  width: 100%;
}

// 饼图
#pie-chart {
  height: 180px;
  width: 100%;
  margin: 10px 0;
}

// 折线图
#line-chart {
  height: 160px;
  width: 100%;
}

.risk-left3 {
  position: relative;
}
</style>