<template>
  <el-aside class="">
    <figure>
      <header class="risk-sts" style="margin-top: 20px">重大危险源</header>
      <div id="danger-sts"></div>
    </figure>
    <figure>
      <header class="risk-sts risk-right2">八大危险作业</header>
      <div id="riskHorBar"></div>
    </figure>
    <figure>
      <header class="risk-sts risk-right3">企业安全标准化</header>
      <ul class="standard flex">
        <li>
          <span class="color-white">一级</span>
          <p style="color: #f37881">93</p>
        </li>
        <li>
          <span class="color-white">二级</span>
          <p style="color: #e48853">56</p>
        </li>
        <li>
          <span class="color-white">三级</span>
          <p style="color: #e5cf6a">28</p>
        </li>
        <li>
          <span class="color-white">未开展</span>
          <p style="color: #538eea">74</p>
        </li>
        <li class="center-pic"></li>
      </ul>
    </figure>
    <figure>
      <header class="risk-sts risk-right2">应急资源统计</header>
      <div id="emerRes"></div>
    </figure>
  </el-aside>
</template>
<script>
import * as echarts from 'echarts'
export default {
  name: 'VisualRightAside',
  props: ['fold'],
  data() {
    return {
      timer: null,
      tag:true
    }
  },
  watch: {
    fold(v) {
      console.log('v:', v)
      if (v) {
        this.init()
      }
    },
  },
  methods: {
    init() {
      let arr = [
        this.riskHorBarInit,
        this.emerResBarInit,
        this.showDangerInit,
      ]
      if (this.tag) {
        arr.forEach((v) => {
          return v()
        })
        this.tag = false
      } else {
        arr[parseInt(Math.random() * arr.length)]()
      }

      // this.$nextTick(function () {
      //   this.riskHorBarInit()
      //   this.emerResBarInit()
      //   this.showDangerInit()
      // })
    },

    // 右侧重大危险源
    showDangerInit() {
      var colors = ['#C53147', '#DA884B', '#FFC849', '#3597BD']
      // let chart = echarts.init(document.getElementById('danger-sts'))
      // 解决echarts控制台警告 echarts.js?1be7:2168 There is a chart instance already initialized on the dom
      //有的话就获取已有echarts实例的DOM节点。
      let chart = echarts.getInstanceByDom(
        document.getElementById('danger-sts')
      )
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('danger-sts'))
      }
      let chartData = [
        {
          name: '一级危险源',
          value: 55,
          value2: 4,
          unit: '家',
        },
        {
          name: '二级危险源',
          value: 2,
          value2: 0,
          unit: '家',
        },
        {
          name: '三级危险源',
          value: 41,
          value2: 3,
          unit: '家',
        },
        {
          name: '四级危险源',
          value: 4,
          value2: 0,
          unit: '家',
        },
      ]
      let arrName = []
      let arrValue = []
      let sum = 0,
        sum2 = 0
      let pieSeries = [],
        lineYAxis = []

      // 数据处理
      chartData.forEach((v, i) => {
        arrName.push(v.name)
        arrValue.push(v.value)
        sum = sum + v.value
        sum2 += v.value2
      })

      // 图表option整理
      chartData.forEach((v, i) => {
        pieSeries.push({
          name: '重大危险源',
          type: 'pie',
          clockwise: false,
          emphasis: {
            //鼠标移入变大
            scale: false,
          },
          radius: [50 - i * 10, 55 - i * 10],
          center: ['24%', '50%'],
          label: {
            position: 'center',
            formatter: `{a|${sum2}}{b|家}`,
            rich: {
              a: {
                color: 'white',
                fontSize: 20,
                padding: 2,
              },
              b: {
                color: '#C0C1C5',
              },
            },
          },
          data: [
            {
              value: sum - v.value,
              name: v.name,
              itemStyle: {
                color: '#2D5878',
              },
            },
            {
              value: v.value,
              name: '',
              itemStyle: {
                color: colors[i],
                borderRadius: '50%',
              },
            },
          ],
        })
        v.percent = ((v.value / sum) * 100).toFixed() + '%'
        lineYAxis.push({
          value: i,
        })
      })

      var option = {
        grid: {
          top: '4%',
          bottom: 0,
          left: '48%',
          containLabel: false,
        },
        yAxis: [
          {
            type: 'category',
            inverse: true,
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              formatter: function (params) {
                let item = chartData[params]
                return `{circle|●}{name|${item.name}}{value|${item.value2}}{unit|家}\n{percent|${item.percent}}`
              },
              interval: 0,
              inside: true,
              color: function (value, index) {
                return colors[index]
              },
              rich: {
                circle: {
                  padding: [0, 10, 0, 0],
                },
                name: {
                  color: '#fff',
                  fontSize: 12,
                },
                percent: {
                  color: '#fff',
                  fontSize: 16,
                  padding: [2, 0, 2, 16],
                },
                value: {
                  color: '#fff',
                  fontSize: 12,
                  padding: [0, 0, 0, 10],
                },
                unit: {
                  color: '#fff',
                  fontSize: 12,
                },
              },
            },
            data: lineYAxis,
          },
        ],
        xAxis: [
          {
            show: false,
          },
        ],
        series: pieSeries,
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 右侧八大危险作业
    riskHorBarInit() {
      var data = [98, 95, 90, 82, 80, 78, 75, 68]
      let chart = echarts.getInstanceByDom(
        document.getElementById('riskHorBar')
      )
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('riskHorBar'))
      }
      let option = {
        grid: {
          top: 10,
          right: 50,
          left: 100,
          bottom: 10,
        },
        legend: {
          show: false,
        },
        xAxis: {
          splitLine: {
            show: false,
          },
          axisLabel: {
            show: false,
          },
        },
        yAxis: {
          type: 'category',
          data: [
            '动火作业',
            '高处作业',
            '吊装作业',
            '临时用电作业',
            '动土作业',
            '断路作业',
            '盲板抽堵作业',
            '受限空间作业',
          ],
          inverse: true,
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          splitLine: {
            show: false,
          },
          axisLabel: {
            color: 'white',
            fontSize: 12,
          },
        },
        series: [
          {
            name: 'X',
            type: 'bar',
            data: data,
            barWidth: 10,
            barGap: 6,
            label: {
              show: true,
              position: 'right',
              valueAnimation: true,
              color: 'white',
            },
            itemStyle: {
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 1,
                y2: 0,
                colorStops: [
                  {
                    offset: 0,
                    color: '#32A9F6', // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: '#63FCFF', // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
          },
        ],
      }
      chart.clear()
      chart.setOption(option)

      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 右侧应急资源统计
    emerResBarInit() {
      // let chart = echarts.init(document.getElementById('emerRes'))
      // 解决echarts控制台警告 echarts.js?1be7:2168 There is a chart instance already initialized on the dom
      //有的话就获取已有echarts实例的DOM节点。
      let chart = echarts.getInstanceByDom(document.getElementById('emerRes'))
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('emerRes'))
      }
      var xData = [
        '应急\n队伍',
        '应急\n物资',
        '应急\n预案',
        '应急\n设备',
        '应急\n专家',
        '保障\n机构',
      ]
      var line = ['375', '500', '900', '800', '610', '1001']
      var line_bg = ['1500', '1500', '1500', '1500', '1500', '1500']
      var option = {
        grid: {
          left: 0,
          right: 0,
          top: 20,
          bottom: 50,
        },

        xAxis: [
          {
            data: xData,
            axisLabel: {
              show: true,
              color: '#fff',
              fontSize: 10,
              lineHeight: 14,
              margin: 20, //刻度标签与轴线之间的距离。
            },

            axisLine: {
              show: false, //不显示x轴
            },
            axisTick: {
              show: false, //不显示刻度
            },
            boundaryGap: true,
            splitLine: {
              show: false,
            },
          },
        ],
        yAxis: [
          {
            show: false,
            splitLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            //柱底圆片
            name: '',
            type: 'pictorialBar',
            symbolSize: [16, 12], //调整截面形状
            symbolOffset: [0, 6],
            z: 12,
            itemStyle: {
              color: '#0D70FF',
            },
            data: line_bg,
          },
          //柱体
          {
            name: '',
            type: 'bar',
            barWidth: 16,
            barGap: '-100%',
            itemStyle: {
              color: '#4898FF',
            },
            showBackground: true,
            backgroundStyle: {
              color: '#063C83',
            },
            data: line,
          },

          //背景柱顶圆片
          {
            name: '',
            type: 'pictorialBar',
            symbolSize: [16, 12], //调整截面形状
            symbolOffset: [0, -6],
            z: 12,
            symbolPosition: 'end',
            itemStyle: {
              color: '#0158A5',
            },
            data: line_bg,
          },

          //柱顶圆片
          {
            name: '',
            type: 'pictorialBar',
            symbolSize: [16, 12], //调整截面形状
            symbolOffset: [0, -6],
            z: 12,
            symbolPosition: 'end',
            itemStyle: {
              color: '#30D8FE',
            },
            data: line,
          },
        ],
      }

      chart.clear()
      chart.setOption(option)
      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },
  },
  mounted() {
    this.init()
     clearInterval(this.timer)
    this.timer = setInterval(() => {
      this.init()
    }, 15000)
  },
}
</script>
<style lang="less" scoped>
// 右侧重大危险源
#danger-sts {
  height: 140px;
  width: 100%;
}

// 八大危险作业
#riskHorBar {
  height: 180px;
}

// 企业安全标准化
.standard {
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  li {
    width: 40%;
    height: 60px;
    margin-right: 10px;
    margin-top: 10px;
    text-align: center;
    background-color: #013a57;

    &.center-pic {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 50px;
      height: 50px;
      background: url(../../assets/images/sd.png) no-repeat;
      background-size: 100% 100%;
    }
  }
  span {
    display: block;
    margin: 8px 0 2px;
    font-size: 12px;
  }

  p {
    font-size: 22px;
  }
}

// 应急资源统计
.risk-right2 {
  margin-top: 10px;
}
#emerRes {
  height: 170px;
}
</style>